.Toolbar {
    height: 60px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    box-sizing: border-box;
    z-index: 90;
    border-bottom: 1px solid #F9F4F5;
}

.Toolbar nav{
    height: 100%;
}

.Logo{
    height: 80%;
}


@media(max-width: 599px){
    .DisplayOnly{
        display: none;
    }
}